import React, { Component } from 'react';
import './styles/moviesSliders.css';
import {connect} from 'react-redux';
import { setSliderMoviesOnState } from '../actions/moviesSlidersActions';
import axios from 'axios';
import $ from 'jquery';
import subsMovie from '../assets/substitute_movie.jpg';
import {Link} from 'react-router-dom';


class MoviesSliders extends Component {
  componentDidMount() {
    this.props.setSliderMoviesOnState(Math.floor(Math.random() * 3) + 1);
    $(document).ready(function()  {
      $(window).scroll(function()  {
      	let  wScroll = $(this).scrollTop();
        let z = 28;
      	$(".slider_second_1_img").css({'transform':'translate(0px, -'+ wScroll/z+'%)'});
        $(".slider_second_2_img").css({'transform':'translate(0px, -'+ wScroll/z+'%)'});
        $(".slider_second_3_img").css({'transform':'translate(0px, -'+ wScroll/z+'%)'});
        $(".slider_second_4_img").css({'transform':'translate(0px, -'+ wScroll/z+'%)'});
        if(window.innerWidth>=768){
          $(".slider_second_5_img").css({'transform':'translate(0px, -'+ wScroll/z+'%)'});
          $(".slider_second_6_img").css({'transform':'translate(0px, -'+ wScroll/z+'%)'});
          $(".slider_second_7_img").css({'transform':'translate(0px, -'+ wScroll/z+'%)'});
          $(".slider_second_8_img").css({'transform':'translate(0px, -'+ wScroll/z+'%)'});
        }
      });
    });
    //this.props.setSliderMoviesOnState()
  }
  render() {
    if(this.props.moviesForSliders.length > 1){
      return (
        <div className="movie_sliders_container_wraper">
          <div className="movie_sliders_container">
            <div className="slider_couple_container">
              <Link to={"/movie/"+this.props.moviesForSliders[1].id}>
                <div className="slider_second_1">
                  <img src={
                    (this.props.moviesForSliders[1].poster_path !== null)?
                    "http://image.tmdb.org/t/p/w185/" + this.props.moviesForSliders[1].poster_path :
                    subsMovie
                  } alt="img" className="slider_second_1_img"/>
                </div>
              </Link>
              <Link to={"/movie/"+this.props.moviesForSliders[2].id}>
              <div className="slider_second_2">
                <img src={
                  (this.props.moviesForSliders[2].poster_path !== null)?
                  "http://image.tmdb.org/t/p/w185/" + this.props.moviesForSliders[2].poster_path :
                  subsMovie
                } alt="img" className="slider_second_2_img"/>
              </div></Link>
              {(window.innerWidth >= 768)?(
                <Link to={"/movie/"+this.props.moviesForSliders[5].id}>
                <div className="slider_second_5">
                  <img src={
                    (this.props.moviesForSliders[5].poster_path !== null)?
                    "http://image.tmdb.org/t/p/w185/" + this.props.moviesForSliders[5].poster_path :
                    subsMovie
                  } alt="img" className="slider_second_5_img"/>
                </div></Link>
              ):null}
              {(window.innerWidth >= 768)?(
                <Link to={"/movie/"+this.props.moviesForSliders[6].id}>
                <div className="slider_second_6">
                  <img src={
                    (this.props.moviesForSliders[6].poster_path !== null)?
                    "http://image.tmdb.org/t/p/w185/" + this.props.moviesForSliders[6].poster_path :
                    subsMovie
                  } alt="img" className="slider_second_6_img"/>
                </div></Link>
              ):null}
            </div>
            <div className="slider_couple_container">
              <Link to={"/movie/"+this.props.moviesForSliders[3].id}>
              <div className="slider_second_3">
                <img src={
                  (this.props.moviesForSliders[3].poster_path !== null)?
                  "http://image.tmdb.org/t/p/w185/" + this.props.moviesForSliders[3].poster_path :
                  subsMovie
                } alt="img" className="slider_second_3_img"/>
              </div>
            </Link>
              <Link to={"/movie/"+this.props.moviesForSliders[4].id}>
              <div className="slider_second_4">
                <img src={
                  (this.props.moviesForSliders[4].poster_path !== null)?
                  "http://image.tmdb.org/t/p/w185/" + this.props.moviesForSliders[4].poster_path :
                  subsMovie
                } alt="img" className="slider_second_4_img"/>
              </div></Link>
              {(window.innerWidth >= 768)?(
                <Link to={"/movie/"+this.props.moviesForSliders[7].id}>
                <div className="slider_second_7">
                  <img src={
                    (this.props.moviesForSliders[7].poster_path !== null)?
                    "http://image.tmdb.org/t/p/w185/" + this.props.moviesForSliders[7].poster_path :
                    subsMovie
                  } alt="img" className="slider_second_7_img"/>
                </div></Link>
              ):null}
              {(window.innerWidth >= 768)?(
                <Link to={"/movie/"+this.props.moviesForSliders[8].id}>
                <div className="slider_second_8">
                  <img src={
                    (this.props.moviesForSliders[8].poster_path !== null)?
                    "http://image.tmdb.org/t/p/w185/" + this.props.moviesForSliders[8].poster_path :
                    subsMovie
                  } alt="img" className="slider_second_8_img"/>
                </div></Link>
              ):null}
            </div>
          </div>
        </div>
      );
    } else return null
  }

}

const mapStateToProps = (state) => {
  return {
    moviesForSliders: state.moviesSlidersReducer.moviesForSliders,
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    setSliderMoviesOnState: (page) => {
      axios.get("https://api.themoviedb.org/3/movie/upcoming?api_key=fa155f635119344d33fcb84fb807649b&language=en-US&page="+page)
      .then(response => {
        dispatch(setSliderMoviesOnState(shuffleArray(response.data.results)))
      })
      .catch(e => console.log(e))
    }
  }
}
const shuffleArray = (a) => {
    for (let i = a.length - 1; i > 0; i--) {
        const j = Math.floor(Math.random() * (i + 1));
        [a[i], a[j]] = [a[j], a[i]];
    }
    return a;
}



export default connect(mapStateToProps,mapDispatchToProps)(MoviesSliders);



