import React, { Component } from 'react';
import './styles/movies.css';
import { Link } from 'react-router-dom';
import rateStart from '../assets/rate_star.svg';
import no_poster from '../assets/no_poster.png';
import $ from 'jquery';
import CircularProgress from 'material-ui/CircularProgress';
class Movies extends Component {
  componentWillMount() {
    //$(".movies_container .movie_holder_link .movie_holder").removeClass('movie_holder_unvisible');
    // setTimeout(() => {
    //
    //
    //   $(".movies_container .movie_holder_link .movie_holder").each(function  (i) {
    //     setTimeout(() => {
    //       $(".movies_container .movie_holder_link .movie_holder").removeClass('movie_holder_unvisible');
    //     },200)
    //   });
    //
    // },200)
  }
  // componentWillReceiveProps(nextProps){
  //   if(nextProps.data !== this.props.data) {
  //
  //
  //     //
  //     // $(".movies_container .movie_holder_link .movie_holder").each(function  (i) {
  //     //   if($(".movie_holder").eq(i).hasClass('movie_come_in_place')){
  //     //     setTimeout(()=>{
  //     //       $(".movies_container .movie_holder_link .movie_holder").eq(i).addClass('movie_come_in_place');
  //     //     }, 150 * (i+1) );
  //     //   }
  //     // });
  //
  //
  //
  //     }
  // }
  displatMoviePoster = (path) => {
    if(path !== undefined && path !== null){
      return  <img alt="" src={"http://image.tmdb.org/t/p/w185/"+path} className="movie_holder_img"/>
    }else return <img alt="" src={no_poster} className="movie_holder_img"/>
  }
  beautifyDate = (dataString) => {// 2017-01-23
    let month = '';
    switch(dataString.substring(5,7)){
      case "01": month = "Jan"; break;
      case "02": month = "Feb"; break;
      case "03": month = "Mar"; break;
      case "04": month = "Apr"; break;
      case "05": month = "May"; break;
      case "06": month = "Jun"; break;
      case "07": month = "Jul"; break;
      case "08": month = "Aug"; break;
      case "09": month = "Sep"; break;
      case "10": month = "Oct"; break;
      case "11": month = "Nov"; break;
      case "12": month = "Dec"; break;
    }
    return  dataString.substring(8,11) + " " + month + " " + dataString.substring(0,4);
  }
  renderMovies = () => {

    if(this.props.data.length > 1){
      console.log("Movies Component data props: ", this.props.data);
      let movies = this.props.data.map((movie,index) => {
        return(
          <Link to={"/movie/"+movie.id} className='movie_holder_link' key={index}>
            <div className="movie_holder ">
              {this.displatMoviePoster(movie.poster_path)}
              <p className="movie_holder_title">{movie.title}</p>
              <p className="movie_holder_rates">
                <img src={rateStart} alt="Rate:" />
                &nbsp;
                {movie.vote_average}
                &nbsp; | &nbsp;
                {movie.vote_count}</p>
                {(this.props.releaseDate)?
                  <p className="release_date_p">{this.beautifyDate(movie.release_date)}</p>
                  :null}
            </div>
          </Link>
        )
      })
      return movies;
    }
  }
  render() {
    return (
      <div className="movies_container_wrapter">
        <div className="circle_progress_container ">
          <CircularProgress size={40} thickness={5} color="#bf2127"/>
        </div>
        <div className="movies_container">
          {(this.props.data.length > 1)?<div className="separator_wraper"><div className="separator"></div></div>:null}
          {this.renderMovies()}
        </div>
      </div>
    );
  }

}

export default Movies;



